<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h2>Wallet Balance Top-up</h2>
        <hr>
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <form>
          <div class="form-group">
            <h4>Top-up value (SGD)</h4>
            <select class="form-control" v-model="topUpValue">
              <option value="20">SGD 20</option>
              <option value="35">SGD 35</option>
              <option value="50">SGD 50</option>
            </select>
          </div>
          <br>
          <div class="form-group">
            <h4>Preferred payment method</h4>
            <select class="form-control" v-model="topUpMethod" required>
              <option value="">-- Select --</option>
              <option value="visa">Visa</option>
              <option value="master">Master</option>
            </select>
          </div>
        </form>
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <h4>Payment summary (SGD)</h4>
        <div class="text-xs-center">
          <h3>
              <b>SGD {{ topUpValue }}</b> with <b>
                <span v-if="topUpMethod.length >= 1">{{ topUpMethod | capitalize }}</span>
                <span v-if="topUpMethod.length<= 0"><small><i>select payment method</i></small></span>
              </b>
          </h3>
          <br>
        </div>
      </div>
    </div>
    <div class="row" v-if="!isTopUpSuccess">
      <div class="col-xs-12">
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="submitTopUpRequest">Submit</button>
      </div>
    </div>
    <div class="row" v-if="isTopUpSuccess">
      <div class="col-xs-12">
        <div class="alert alert-success" role="alert">
          Success top-up! 
            <router-link
              :to="{ name: 'wallet' }"
            >
              Click here to view your wallet
            </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "viewTopUpWallet",
  data () {
    return {
      isTopUpSuccess: false,
      topUpValue: 20,
      topUpMethod: ''
    }
  },
  methods: {
    submitTopUpRequest () {
      // console.log(this.topUpValue)
      // console.log([20, 35, 50].includes(parseInt(this.topUpValue)))
      if(!([20, 35, 50]).includes(parseInt(this.topUpValue))){
        alert("Please select valid top-up value")
        return;
      }else if(!(['VISA', 'MASTER']).includes(this.topUpMethod.toUpperCase())){
        alert("Please select valid preferred payment method")
        return;
      }
      this.isTopUpSuccess = true;
    }
  }
};
</script>

<style scoped>
</style>


